/**
 *  @Title   高级表格
 *  @Auther  YuRonghui
 *  @Des     描述
 *  @Time    2019
 *  @Params
 *  columns:Array
 *  pageSize:Number
 *  parseParams: Function pagination, filters, sorter
 *  parseResult: Function res
 *  rowKey: Function record
 *  api: String
 *  scroll: Object
 */
import React, { Component } from "react";
import { Table, } from "antd";
import Apis from "@/apis";
import PropTypes from "prop-types";
import { arrToTree, filterObj } from "../utils/fun";
import "./style.less";

class AdvanceTable extends Component {
  static propTypes = {
    pageSizeOptions: PropTypes.array,
    pageSize: PropTypes.number,
    current: PropTypes.number,
    isTree: PropTypes.bool,
    totalApi: PropTypes.string,
    params: PropTypes.object,
    response: PropTypes.func,
    sort: PropTypes.object,
    rowKey: PropTypes.func,
    api: PropTypes.string,
    tbName: PropTypes.string,
    onChange: PropTypes.func,
    onFetched: PropTypes.func,
  };

  static defaultProps = {
    pageSize: 20,
    current: 1,
    sort: {},
    params: {},
    response: (res) => res,
    rowKey: (record) => record._id,
    api: "",
    tbName: "",
    totalApi: '',
    isTree: false,
  };
  updateAt = 0
  state = {
    data: [],
    columns: [],
    showSizeChanger: true,
    pageSizeOptions: ["10", "20", "50", "100", "200"],
    pageSize: 20,
    current: 1,
    total: 0,
    loading: false,
    sort: "",
    filter: [],
  };

  componentDidMount() {
    this.initState(this.props)
  }

  initState(props) {
    const { pageSize, current, showSizeChanger, pageSizeOptions = [], } = props;
    this.setState(
      {
        pageSize: pageSize || this.state.pageSize,
        current: current || this.state.current,
        showSizeChanger: showSizeChanger || this.state.showSizeChanger,
        pageSizeOptions:
          Array.isArray(pageSizeOptions) && pageSizeOptions.length
            ? pageSizeOptions
            : this.state.pageSizeOptions,
      },
      () => {
        this.fetch();
      }
    );
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { update, params = {} } = nextProps
    if (update !== this.updateAt) {
      this.updateAt = update
      this.fetch(params);
    }
  }

  handlePaginationChange = (page, pageSize) => {
    let { onChangePage } = this.props;
    this.setState({
      current: page,
      pageSize: pageSize,
    }, () => {
      if (onChangePage) onChangePage(page, pageSize);
      this.fetch();
    });
  };

  fetch = async (body) => {
    this.setState({ loading: true });
    const { api, params = {}, response, isTree, onFetched, totalApi, tbName, } = this.props;
    if (!api) return;
    if (typeof Apis[api] === "function") {
      let { current, pageSize, sort, filter } = this.state;
      if (sort) params._sort = sort;
      let query = {
        _limit: pageSize,
        _start: pageSize * (current - 1),
        _where: filter ? filter : undefined,
        ...filterObj({ ...params, ...body }, val => val !== undefined && val !== '')
      }
      let res = await Apis[api](query, tbName);
      let total = totalApi ? await Apis[totalApi](query, tbName) : undefined
      if (res) {
        let results = response ? response(res, current, pageSize) : {};
        if (Array.isArray(results)) {
          let data = isTree ? arrToTree(results, "") : results;
          if (typeof onFetched === "function") onFetched(data);
          this.setState({ loading: false, data, total });
        }
      }
    }
  };

  handleTableChange = (pagination, filters, sorter) => {
    // console.warn(filters, sorter);
    let sort = "",
      filter = [];
    if (sorter.field && sorter.order) {
      sort = `${sorter.field}:${sorter.order.replace("end", "")}`;
      this.setState({ sort }, () => {
        this.fetch();
      });
    }
    if (Object.keys(filters).length) {
      for (let key of Object.keys(filters)) {
        if (Array.isArray(filters[key])) {
          for (let val of filters[key]) {
            let obj = {};
            obj[`${key}_in`] = val;
            filter.push(obj);
          }
        } else if (filters[key]) {
          let obj = {};
          obj[`${key}_in`] = filters[key];
          filter.push(obj);
        }
      }
      this.setState({ filter });
    }
  };

  render() {
    let { rowKey, dataSource = [], showSearch, onChangePage, columns, ...otherProps } = this.props;
    let { showSizeChanger, pageSizeOptions, pageSize, current, total, data = [] } = this.state;
    let tableData = dataSource.length ? dataSource : data
    return (
      <div className="advance-table">
        <Table
          columns={columns.filter((item) => !item.hidden)}
          rowKey={rowKey}
          dataSource={tableData}
          pagination={{
            showSizeChanger,
            pageSizeOptions,
            pageSize,
            current,
            total,
            showTotal: total => `总计 ${total} 条`,
            onChange: this.handlePaginationChange,
          }}
          loading={this.state.loading}
          onChange={this.handleTableChange}
          {...otherProps}
        />
      </div>
    );
  }
}

export default AdvanceTable;
